export const getInvoice = (invoice) => ({
     type: 'GET_INVOICE', 
     payload: invoice
});

export const setInvoices = (invoices) => ({
     type: 'SET_INVOICES', 
     payload: invoices
});

export const setDialog = (invoiceDialog) => ({
     type: 'SET_INVOICE_DIALOG', 
     payload: invoiceDialog
});