import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import { StyleSheet, Text, View, Platform } from "react-native";

import { getInvoices } from '../../providers/api'
import { stringToaddDate, formatToBRL } from '../../providers/commonMethods'
import { setDialog } from '../../actions/invoiceAction'
import FullScreenInvoiceDialog from '../../components/Layout/Dialogs/FullScreenInvoiceDialog'
import ListLoading from '../../components/Loading/ListLoading'
/** Assets */
import { DataGrid, RowsProp, ColDef } from '@material-ui/data-grid';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Slide from '@material-ui/core/Slide';

class Invoices extends Component {
    state = {
        invoices: undefined,
        item: undefined,
        dialog: false
    }
    //
    Transition = React.forwardRef(function Transition(props, ref) {
      return <Slide direction="up" ref={ref} {...props} />;
    });

    async componentDidMount() {
        const response = await getInvoices();
        this.setState({ ...this.state, invoices: response });
    }

    dialogHandler(item) {
        this.props.setDialog(true);
        this.setState({ ...this.state, item });
    }

    render() {
        let { invoices, item } = this.state;

          const columns = [
            { id: 'title', label: 'Titulo' },
            {
              id: 'status',
              label: 'Situação',
              minWidth: 170,
              align: 'right',
              format: (value) => {
                //value.length > 50 ? value.substring(0,50) + "..." : value
                const status = [
                  "Em Aberto",
                  "Pendente",
                  "Processando",
                  "Pago",
                  "Pago"
                ]
                return status[value]
              },
            },
            {
              id: 'price',
              label: 'Valor',
              minWidth: 170,
              align: 'right',
              format: (value) => 'R$ ' + formatToBRL(value),
            },
            {
              id: 'created_at',
              label: 'Validade',
              minWidth: 170,
              align: 'right',
              format: (value) => stringToaddDate(value,"DD/MM/yyyy",{ qtd: 1, period: 'M'}),
            },
          ];
        return (
            <View>
              <Fragment>
              { invoices === undefined ? (<ListLoading />) :   (
                 <Paper>
                    <TableContainer style={{
                          margin: 0,
                          width: '100%',
                        }}>
                      <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                          <TableRow>
                            {columns.map((column) => (
                              <TableCell
                                key={column.id}
                                align={column.align}
                                style={{ minWidth: column.minWidth }}
                              >
                                {column.label}
                              </TableCell>
                            ))}
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {invoices.data.map(row => {
                            return (
                              <TableRow hover role="checkbox" tabIndex={-1} key={row.Id} onClick={() => this.dialogHandler(row)}>
                                {columns.map((column) => {
                                  const value = row[column.id];
                                  return (
                                    <TableCell key={column.id} align={column.align}>
                                      {column.format ? column.format(value) : value}
                                    </TableCell>
                                  );
                                })}
                              </TableRow>
                            );
                          })}
                        </TableBody>
                      </Table>
                    </TableContainer>
                  </Paper>)}
                <FullScreenInvoiceDialog item={item} />
                </Fragment>
            </View>
        )
    }
}
const mapStateToProps = store => ({
  invoiceDialog: store.invoiceReducer.invoiceDialog
  });
const mapDispatchToProps = dispatch =>
  bindActionCreators({ setDialog }, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Invoices)