import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import CardDialogLoading from '../../Loading/CardDialogLoading'
import { StyledAppBar } from '../../Layout/Header/style'
import { PrimaryButton } from '../../Custom'

import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import Dialog from '@material-ui/core/Dialog';

import { postCheckout, getInvoices } from '../../../providers/api'
import {setDialog, getInvoice} from '../../../actions/invoiceAction'

import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import { FormatAlignLeftSharp } from '@material-ui/icons';
import invoicesReducer from '../../../reducers/invoicesReducer';

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop:84,
  },
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  media: {
    height: 140,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function FullScreenInvoiceDialog(props) {
  const classes = useStyles();
  
  const handleClose = () => {
    setInvoice(undefined);
    props.setDialog(false);
  };

  const { item, iDialog } = props;
  const [invoice, setInvoice] = useState(undefined);
  const doEffect = async () => { 
    if(props.item !== undefined )
      setInvoice( await (await getInvoices(props.item.Id)).data)
  }
  
  useEffect(() => { doEffect() }, [props.item]);
  const status = [
    {message : "Em Aberto", color: "#F1F1F1F1"},
    {message : "Pendente", color: "#FF8C00"},
    {message : "Processando", color: "#0000FF"},
    {message : "Pago", color: "#32CD32"},
    {message : "Pago", color: "#32CD32"}
  ]

  const getBol = async (id) => {
    let data = await postCheckout(id);
    console.log(data);
  }

  let createMarkup = () => {
    let url =  invoice === undefined ? "about:us1" 
    : invoice.payment === "" ? "about:us2" : invoice.payment
    return {__html: '<iframe src="'+ url + '" width="100%" height="350"></iframe>'};
  }
  return (
    <div>
      <Dialog fullScreen open={iDialog} onClose={() => handleClose()} TransitionComponent={Transition}>
        <StyledAppBar position="fixed">
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={() => handleClose()} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              {invoice === undefined ? ("") : (invoice.title)}
            </Typography>
          </Toolbar>
        </StyledAppBar>
        { invoice === undefined ? (<CardDialogLoading />) : (
        <Card className={classes.root}>
          <CardActionArea>
          <Typography gutterBottom variant="h5" component="h2">
              { invoice.payment !== "" && invoice.payment.length > 5 ? (
              <div dangerouslySetInnerHTML={createMarkup()}/>):
              ("")}
              </Typography>
            <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                {invoice.title} <Chip style={{ background: status[invoice.type].color}} label={status[invoice.type].message} />
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  {invoice.description}
                </Typography>
                <Typography gutterBottom variant="h5" component="h2">
                  R$ {invoice.price}
                </Typography>
            </CardContent>
          </CardActionArea>
          <CardActions>
            {
            invoice.payment === "" ? (
            <PrimaryButton variant="contained" fullwidth size="big" fullWidth color="thirty" 
            onClick={() => getBol({id : invoice.Id})}>
              Gerar Boleto
            </PrimaryButton>
            ):("")
            }
          </CardActions>
        </Card>
        )}
      </Dialog>
    </div>
  );
}
const mapStateToProps = store => ({
  iDialog: store.invoiceReducer.invoiceDialog,
});
const mapDispatchToProps = dispatch =>
bindActionCreators({ postCheckout, setDialog }, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(FullScreenInvoiceDialog)