const initialState = { invoiceDialog: false, invoices: [], invoice : undefined }

export default (state = initialState, action) => {
    switch (action.type) {
        case 'SET_INVOICES' :
            return { ...state, invoices: action.payload }
        case 'SET_INVOICE_DIALOG':
            return { ...state, invoiceDialog: action.payload }
        case 'GET_INVOICE':
            return { ...state, invoice: action.payload }
        default:
            return state
    }
}