import React, { useState, useEffect }  from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
/** Assets */

import { getInvoices } from '../../../providers/api'
import { setInvoices } from '../../../actions/invoiceAction';
import {setSnackbar} from '../../../actions/appActions'
//import './index.css'

import Snackbar from '@material-ui/core/Snackbar';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

function Footer(props) {
  let data = [];
  const doSome = async () => {
    if(props.invoices.length === 0){
      const response = await getInvoices();
      //console.log(response.data)
      props.setInvoices(response.data);
    }
  }
  // useEffect(() =>{doSome();}, [data])
  const classes = useStyles();
  return (
    <div className={classes.root}>
      
    </div>
  );
}


const mapStateToProps = store => ({
  snackbar: store.appReducer.snackbar,
  invoices: store.invoiceReducer.invoices
});
const mapDispatchToProps = dispatch =>
bindActionCreators({ setInvoices, setSnackbar}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Footer)